import { CanActivateFn, Router } from '@angular/router';
import { inject, PLATFORM_ID } from '@angular/core';
import { isPlatformBrowser } from '@angular/common';
import { ToastService } from '../common/toast.service';

export const authGuard: CanActivateFn = (route, state) => {
  const router = inject(Router);
  const platformId = inject(PLATFORM_ID);

  if (isPlatformBrowser(platformId)) {
    const token = localStorage.getItem('authToken');
    const role = localStorage.getItem('userRole') ? atob(localStorage.getItem('userRole')!) : '';
    const status = localStorage.getItem('userStatus') ? atob(localStorage.getItem('userStatus')!) : '';
      const toastService = inject(ToastService); // ‚úÖ inject service here
    if (token) {
      const expectedRoles = route.data?.['roles'] as string[];
      const expectedStatuses = route.data?.['statuses'] as string[];

      // ‚ùå Role mismatch
      if (expectedRoles && !expectedRoles.includes(role)) {
        if (role === 'admin' && expectedRoles.includes('user')) {
          // ‚úÖ Show popup for admin trying to access user side
          toastService.showToast(
            'You are logged in as admin. To visit this page, login with member credentials.',
            'danger'
          );
        } else {
          router.navigateByUrl('/unauthorized', { replaceUrl: true });
        }
        return false;
      }

      // ‚ùå Status mismatch
      if (expectedStatuses && !expectedStatuses.includes(status)) {
        if (status === 'created') {
          router.navigateByUrl('/user-not-aproved', { replaceUrl: true });
        } else {
          router.navigateByUrl('/unauthorized', { replaceUrl: true });
        }
        return false;
      }

      return true; // ‚úÖ Allowed
    }

    // Not logged in ‚Üí save URL & redirect
    localStorage.setItem('redirectUrl', state.url);
    router.navigateByUrl('', { replaceUrl: true });
    return false;
  }

  return false;
};
