import { CommonModule } from '@angular/common';
import { Component } from '@angular/core';
import {
  FormBuilder,
  FormGroup,
  FormsModule,
  ReactiveFormsModule,
  Validators,
  FormControl
} from '@angular/forms';
import { Router, RouterModule } from '@angular/router';
import { HttpClient, HttpClientModule } from '@angular/common/http';
import { IasLoaderComponent } from "../../common/ias-loader/ias-loader.component";
import { environment } from '../../../environments/environment';
import { ToastService } from '../../common/toast.service';
declare var bootstrap: any;

@Component({
  selector: 'app-ias-login',
  standalone: true,
  imports: [
    CommonModule,
    FormsModule,
    ReactiveFormsModule,
    RouterModule,
    HttpClientModule,
    IasLoaderComponent
  ],
  templateUrl: './ias-login.component.html',
  styleUrls: ['./ias-login.component.scss'],
})
export class IasLoginComponent {
  showPassword = false;
  emailForm: FormGroup;
  otpForm: FormGroup;
  loading = false;
  errorMessage = '';
  showOtpStep = false;
  otpControls = ['d1', 'd2', 'd3', 'd4', 'd5', 'd6'];
  countdown!: number;
  intervalId: any;
  isResendDisabled = false;
  tempToken: string = '';
  toastMessage = '';
  toastType: string = 'primary';
  constructor(
    private fb: FormBuilder,
    private http: HttpClient,
    private router: Router,
    private toastService: ToastService,
  ) {
    this.emailForm = this.fb.group({
      email: ['', [Validators.required, Validators.email, Validators.pattern(/^[a-zA-Z0-9._%+-]+@[a-zA-Z0-9.-]+\.[A-Za-z]{2,}$/)]],
      password: ['', Validators.required],
    });

    this.otpForm = this.fb.group({});
    this.otpControls.forEach((ctrl) =>
      this.otpForm.addControl(ctrl, new FormControl('', [Validators.required, Validators.pattern('[0-9]')]))
    );
  }

  get email() {
    return this.emailForm.get('email');
  }

  get password() {
    return this.emailForm.get('password');
  }

  togglePasswordVisibility() {
    this.showPassword = !this.showPassword;
  }

  // Step 1: Login
  onSubmit() {
    if (this.emailForm.valid) {
      this.loading = true;

      const payload = {
        email: this.email?.value,
        password: this.password?.value,
      };

      this.http
        .post(`${environment.apiUrl}/accounts/login`, payload)
        .subscribe({
          next: (res: any) => {
            this.loading = false;
            const token = res?.data?.token;
            if (token) {
              this.tempToken = token;
              this.showOtpStep = true;
              this.showToast('‚úÖ Login successful, please verify OTP.', 'success');
              this.startCountdown(59);
            } else {
              this.showToast('‚ö†Ô∏è No access token received.', 'warning');
            }
          },
          error: (err) => {
            this.loading = false;
            this.showToast(err.error?.message || '‚ùå Invalid email or password.', 'danger');
          },
        });
    } else {
      this.emailForm.markAllAsTouched();
      this.showToast('‚ö†Ô∏è Please fill in all required fields.', 'warning');
    }
  }

  showToast(message: string, type: string = 'primary') {
    this.toastMessage = message;
    this.toastType = type;

    const toastEl = document.getElementById('liveToast');
    if (toastEl) {
      const toast = new bootstrap.Toast(toastEl, { delay: 3000 });
      toast.show();
    }
  }
  startCountdown(seconds: number) {
    this.countdown = seconds;
    this.isResendDisabled = true;

    clearInterval(this.intervalId);
    this.intervalId = setInterval(() => {
      if (this.countdown > 0) {
        this.countdown--;
      } else {
        this.isResendDisabled = false;
        clearInterval(this.intervalId);
      }
    }, 1000);
  }
  onResendOtp() {
    // üëâ call your API to resend OTP here

    this.startCountdown(59); // restart timer
  }
  handleKeyDown(event: KeyboardEvent, index: number) {
    const input = event.target as HTMLInputElement;

    // ‚úÖ allow navigation keys
    const allowedKeys = ['Backspace', 'Delete', 'ArrowLeft', 'ArrowRight', 'Tab'];
    if (allowedKeys.includes(event.key)) {
      // Handle backspace navigation
      if (event.key === 'Backspace' && !input.value && index > 0) {
        const prev = input.parentElement?.children[index - 1] as HTMLInputElement | undefined;
        prev?.focus();
      }
      return; // allow
    }

    // ‚úÖ block non-digits
    if (!/^[0-9]$/.test(event.key)) {
      event.preventDefault();
    }
  }


  // OTP Field Navigation
  onOtpInput(event: any, index: number) {
    const input = event.target;
    if (input.value && index < this.otpControls.length - 1) {
      const next = document.querySelectorAll<HTMLInputElement>('.otp-inputs input')[index + 1];
      next?.focus();
    }
  }

  // Step 2: Verify OTP
submitOtp() {
  if (this.otpForm.valid) {
    this.loading = true;

    const otpValue = this.otpControls
      .map(ctrl => this.otpForm.get(ctrl)?.value)
      .join('');

    const otpPayload = {
      otp: otpValue,
      action: 'login',
      token: this.tempToken
    };

    this.http.patch(`${environment.apiUrl}/accounts/otpverify`, otpPayload)
      .subscribe({
        next: (res: any) => {
          this.loading = false;

          const { sessionToken, userDetails } = res?.data || {};
          const { email, role, status } = userDetails || {};

          if (sessionToken) {
            // ‚úÖ Save session token
            localStorage.setItem('authToken', res.data.sessionToken);
            localStorage.setItem('refreshToken', res.data.refreshToken);
            localStorage.setItem('expireAt', res.data.expireAt);
            localStorage.setItem('isLoggedIn', 'true');

            // Encode email and role in Base64
            if (email) localStorage.setItem('userEmail', btoa(email));
            if (role) localStorage.setItem('userRole', btoa(role));
            if (status) localStorage.setItem('userStatus', btoa(status));

            // ‚úÖ Show success toast
            this.toastService.showToast('Welcome to IASCA portal', 'success');

            // üîë Check for redirectUrl
            const redirectUrl = localStorage.getItem('redirectUrl');

            if (redirectUrl) {
              localStorage.removeItem('redirectUrl');
              this.router.navigateByUrl(redirectUrl);
            } else {
              this.router.navigate(['homepage']);
            }

          } else {
            this.errorMessage = 'No session token received after OTP verification.';
          }
        },
        error: (err) => {
          this.loading = false;
          this.showToast(err.error?.message || '‚ùå Invalid OTP. Please try again.', 'danger');
          this.errorMessage = err.error?.message || 'Invalid OTP. Please try again.';
        }
      });
  } else {
    this.otpForm.markAllAsTouched();
  }
}

}
