import { CommonModule, DatePipe } from '@angular/common';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import {  Component, OnDestroy, OnInit } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, ReactiveFormsModule, Validators } from '@angular/forms';
import { RouterModule } from '@angular/router';
import { environment } from '../../../environments/environment';
import { IasLoaderComponent } from "../../common/ias-loader/ias-loader.component";

declare var bootstrap: any;
@Component({
  selector: 'app-ias-signup',
  standalone: true,
  imports: [CommonModule, ReactiveFormsModule, RouterModule, IasLoaderComponent],
  templateUrl: './ias-signup.component.html',
  styleUrl: './ias-signup.component.scss',
  providers: [DatePipe]
})
export class IasSignupComponent implements OnInit, OnDestroy {

  currentStep = 1;
  emailForm: FormGroup;
  memberidForm: FormGroup;
  memberdetailsForm: FormGroup;
  token: string | null = null;
  sessionid: any = null;
  otpForm: FormGroup;
  passwordForm: FormGroup;
  otpControls = ['d1', 'd2', 'd3', 'd4', 'd5', 'd6'];
  countdown!: number;
  intervalId: any;
  isResendDisabled = false;
  showNewPassword = false;
  showConfirmPassword = false;
  passwordnotmatch = false;
  action: string = '';
  loading: boolean = false;
  mobilenumber: string | null = null;
  alternateemail: string | null = null;
  iasid: string | null = null;
  iasname: string | null = null;
  iasbatch: string | null = null;
  iascadre: string | null = null;
  iasprofilePic: string | null = null;
  toastMessage = '';
  toastType: string = 'primary';
  private myModal: any;
  cadrelist: any[] = []; // Example cadre list, replace with actual data
  maxDOB: string | undefined;
  constructor(private fb: FormBuilder, private http: HttpClient, private datePipe: DatePipe) {
    this.emailForm = this.fb.group({
      email: ['', [Validators.required, Validators.email, Validators.pattern(/^[a-zA-Z0-9._%+-]+@[a-zA-Z0-9.-]+\.[A-Za-z]{2,}$/)]]
    });
    this.memberidForm = this.fb.group({
      name: ['', [Validators.pattern(/^[A-Za-z\s]+$/)]],
      identityNumber: ['', [Validators.required,]],
      alternateEmail: [null, [Validators.pattern(/^[a-zA-Z0-9._%+-]+@[a-zA-Z0-9.-]+\.[A-Za-z]{2,}$/)]],
      mobilenumber: [null, [Validators.pattern(/^[0-9]{10}$/)]]

    });
    this.memberdetailsForm = this.fb.group({
      fullname: [''],
      cadre: [0, [Validators.required]],
      allotmentyear: ['', [Validators.required, Validators.pattern(/^[0-9]{4}$/)]],
      gender: ['', [Validators.required]],
      dateofbirth: ['', [Validators.required]],
      mothertongue: ['', [Validators.required]],
      retirementreason: ['', [Validators.required]],
      mobilenumber: [null, [Validators.pattern(/^[0-9]{10}$/)]],
      alternateEmail: [null,  [Validators.pattern(/^[a-zA-Z0-9._%+-]+@[a-zA-Z0-9.-]+\.[A-Za-z]{2,}$/)]],
    });

    const controls: any = {};
    this.otpControls.forEach(ctrl => {
      controls[ctrl] = new FormControl('', [Validators.required, Validators.pattern(/^[0-9]$/)]);
    });
    this.otpForm = this.fb.group(controls);

    this.passwordForm = this.fb.group({
      newPassword: ['', [
        Validators.required,
        Validators.minLength(6),
        Validators.pattern(/^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)(?=.*[@$!%*?&#])[A-Za-z\d@$!%*?&#]{8,}$/)
      ]
      ],
      confirmPassword: ['', [Validators.required]]
    });
  }
  startCountdown(seconds: number) {
    this.countdown = seconds;
    this.isResendDisabled = true;

    clearInterval(this.intervalId);
    this.intervalId = setInterval(() => {
      if (this.countdown > 0) {
        this.countdown--;
      } else {
        this.isResendDisabled = false;
        clearInterval(this.intervalId);
      }
    }, 1000);
  }
  onResendOtp() {
    // üëâ call your API to resend OTP here

    this.startCountdown(59); // restart timer
  }
  ngOnInit() {
    const url = `${environment.apiUrl}/dopt/cadres?limit=0`;
    this.http.get(url).subscribe({
      next: (response: any) => {
        console.log('cadeeeeessss', response);
        this.loading = false;
        this.cadrelist = response.data.data;
        console.log('Cadre List:', this.cadrelist);
      },
    });
      const today = new Date();
  today.setFullYear(today.getFullYear() - 21); // subtract 21 years
  this.maxDOB = today.toISOString().split('T')[0]; // format: YYYY-MM-DD
  }
  // ngAfterViewInit(): void {
  //   const modalEl = document.getElementById('Warning');
  //   if (modalEl) {
  //     this.myModal = new bootstrap.Modal(modalEl, {
  //       backdrop: 'static',
  //       keyboard: false
  //     });
  //     this.myModal.show();
  //   }
  // }

  ngOnDestroy(): void {
    if (this.myModal) {
      this.myModal.hide();     // hide modal
      this.myModal.dispose();  // clean up bootstrap instance
      this.myModal = null;
    }
  }
  // Step 1 - Submit email
  submitMemberId() {
    if (this.memberidForm.valid) {
      console.log('Member ID submitted:', this.memberidForm.value);
      this.loading = true;
      const url = `${environment.apiUrl}/dopt/officers?limit=1&identityNo=` + this.memberidForm.value.identityNumber;
      this.http.get(url).subscribe({
        next: (response: any) => {
          console.log('user details', response);
          this.iasid = response.data.data[0]?.id; // Store the token for later use
          this.iasname = response.data.data[0]?.englishName;
          this.iasbatch = response.data.data[0]?.allotmentYear;
          this.iascadre = response.data.data[0]?.cadreDomicile;
          this.iasprofilePic = response.data.data[0]?.profilePic;
          console.log('IAS ID:', this.iasid);
          this.loading = false;
          if (this.iasid !== undefined && this.iasid !== null) {
            console.log('Valid IAS ID found:', this.iasid);
            this.mobilenumber = this.memberidForm.value.mobilenumber || null;
            this.alternateemail = this.memberidForm.value.alternateEmail || null;
            this.showToast('‚úÖ IAS Details Found', 'success');
            this.currentStep = 6;
          }
          else {
            this.loading = false;
            this.showToast('IAS Details Not Found', 'danger');
          }
        },
        error: (err) => {
          this.loading = false;
          this.showToast('Error fetching IAS details', 'danger');
        }
      });
    }
  }
  showToast(message: string, type: string = 'primary') {
    this.toastMessage = message;
    this.toastType = type;

    const toastEl = document.getElementById('liveToast');
    if (toastEl) {
      const toast = new bootstrap.Toast(toastEl, { delay: 3000 });
      toast.show();
    }
  }
 allow10Numbers(event: KeyboardEvent) {
  const charCode = event.key;

  // Allow only digits 0‚Äì9
  if (!/^[0-9]$/.test(charCode)) {
    event.preventDefault();
  }

  // Limit to 10 digits
  const input = event.target as HTMLInputElement;
  if (input.value.length >= 10) {
    event.preventDefault();
  }
}

  allowOnlyLetters(event: KeyboardEvent) {
  const charCode = event.key.charCodeAt(0);
  // Allow: a-z, A-Z, and space
  if (!/[a-zA-Z\s]/.test(event.key)) {
    event.preventDefault();
  }
}
validateYear(event: any) {
  const input = event.target as HTMLInputElement;
  let value = input.value;

  // Allow only 4 digits
  if (value.length > 4) {
    value = value.slice(0, 4);
  }

  // Block future years
  const currentYear = new Date().getFullYear();
  if (+value > currentYear) {
    value = currentYear.toString();
  }

  input.value = value;
}

  submitmemberdetails() {
    if (this.memberdetailsForm.valid) {
      console.log('Member ID submitted:', this.memberdetailsForm.value);
      this.loading = true;
      console.log('Date of Birth:', this.memberdetailsForm.value.dateofbirth);

      const formatted = this.datePipe.transform(this.memberdetailsForm.value.dateofbirth, 'dd/MM/yyyy');
      console.log('Formatted Date of Birth:', formatted);
      const url = `${environment.apiUrl}/dopt/officers?limit=1&englishName=` + this.memberdetailsForm.value.fullname + '&populate=cadre' + '&cadre=' + this.memberdetailsForm.value.cadre + '&allotmentYear=' + this.memberdetailsForm.value.allotmentyear + '&gender=' + this.memberdetailsForm.value.gender + '&dob=' + formatted + '&mothertongue=' + this.memberdetailsForm.value.mothertongue + '&retirementreason=' + this.memberdetailsForm.value.retirementreason;
      this.http.get(url).subscribe({
        next: (response: any) => {
          console.log('user details', response);
          this.iasid = response.data.data[0]?.id; // Store the token for later use
          this.iasname = response.data.data[0]?.englishName;
          this.iasbatch = response.data.data[0]?.allotmentYear;
          this.iascadre = response.data.data[0]?.cadreDomicile;
          this.iasprofilePic = response.data.data[0]?.profilePic;
          console.log('IAS ID:', this.iasid);
          this.loading = false;
          if (this.iasid !== undefined && this.iasid !== null) {
            console.log('Valid IAS ID found:', this.iasid);
            this.mobilenumber = this.memberdetailsForm.value.mobilenumber || null;
            this.alternateemail = this.memberdetailsForm.value.alternateEmail || null;
            this.showToast('‚úÖ IAS Details Found', 'success');
            this.currentStep = 6;
          }
          else {
            this.loading = false;
            this.showToast('IAS Details Not Found', 'danger');
          }
        },
        error: (err) => {
          this.loading = false;
          this.showToast('Error fetching IAS details', 'danger');
        }
      });
    }
  }
  submitdetails() {
    const url = `${environment.apiUrl}/accounts/details`;
    const payload = {
      identityNumber: {
        id: this.iasid
      },
      alternateEmail: this.alternateemail !== '' ? this.alternateemail : null,
      mobileNumber: (this.mobilenumber !== '' && this.mobilenumber != null)
        ? String(this.mobilenumber)
        : null
    }
    const headers = new HttpHeaders({
      'Authorization': `Bearer ${this.sessionid}`,
      'Content-Type': 'application/json'
    });
    this.http.post(url, payload, { headers }).subscribe({
      next: (response: any) => {
        console.log('user details', response);
        console.log('message', response.data.message);
        this.loading = false;
        this.showToast('‚úÖ IAS Details submitted successful', 'success');
        this.currentStep = 7;
      },
      error: (err) => {
        this.loading = false;
        this.showToast('This Dopt officer is already linked to another user', 'danger');
      }
    });
  }
  submitEmail() {
    if (this.emailForm.valid) {
      this.loading = true;
      const url = `${environment.apiUrl}/accounts/signup`;
      const payload = {
        email: this.emailForm.value.email
      }
      this.http.post(url, payload).subscribe({
        next: (response: any) => {
          console.log('Signup successful', response);
          this.token = response.data.token; // Store the token for later use
          this.action = response.data.action; // Store the action for late
          this.loading = false;
          this.showToast('‚úÖ Please check your email for OTP.', 'success');
          this.currentStep = 2;
          this.startCountdown(59);
        },
        error: (err) => {
          this.loading = false;
          this.showToast('Email is already registered.', 'danger');
        }
      });
    }
  }
  onOtpInput(event: Event, index: number) {
    const input = event.target as HTMLInputElement;
    if (input.value && index < this.otpControls.length - 1) {
      const next = input.parentElement?.children[index + 1] as HTMLInputElement | undefined;
      next?.focus();
    }
  }
  handleKeyDown(event: KeyboardEvent, index: number) {
    const input = event.target as HTMLInputElement;

    // ‚úÖ allow navigation keys
    const allowedKeys = ['Backspace', 'Delete', 'ArrowLeft', 'ArrowRight', 'Tab'];
    if (allowedKeys.includes(event.key)) {
      // Handle backspace navigation
      if (event.key === 'Backspace' && !input.value && index > 0) {
        const prev = input.parentElement?.children[index - 1] as HTMLInputElement | undefined;
        prev?.focus();
      }
      return; // allow
    }

    // ‚úÖ block non-digits
    if (!/^[0-9]$/.test(event.key)) {
      event.preventDefault();
    }
  }




  togglePassword(field: 'new' | 'confirm') {
    if (field === 'new') {
      this.showNewPassword = !this.showNewPassword;
    } else {
      this.showConfirmPassword = !this.showConfirmPassword;
    }
  }
  submitOtp() {
    if (this.otpForm.valid) {
      this.loading = true;
      const otp = this.otpControls.map(ctrl => this.otpForm.get(ctrl)?.value).join('');
      console.log('OTP entered:', otp);
      const url = `${environment.apiUrl}/accounts/otpverify`;
      const token = this.token;
      const headers = new HttpHeaders({
        'Authorization': `Bearer ${token}`,
        'Content-Type': 'application/json'
      });
      const payload = {
        otp: otp,
        action: this.action, // Use the action stored from the signup response
        token: this.token // Include the token in the payload
      }
      console.log('otp payload', payload);

      this.http.patch(url, payload, { headers }).subscribe({
        next: (response: any) => {
          console.log('otp verified ', response);
          this.sessionid = response.data.sessionToken; // Store the session ID for later use
          this.loading = false;
          this.showToast('‚úÖ OTP verified successfully', 'success');
          this.currentStep = 3; // ‚úÖ move to password step
        },
        error: (err) => {
          this.loading = false;
          this.showToast('Failed to verify OTP', 'danger');
        }
      });
    }
  }

  submitPassword() {
    if (this.passwordForm.valid) {
      if (this.passwordForm.value.newPassword !== this.passwordForm.value.confirmPassword) {
        this.passwordnotmatch = true;
        return;
      }
      console.log('Password created:', this.passwordForm.value);
      const formdata = this.passwordForm.value;
      const url = `${environment.apiUrl}/accounts/setup-password`;
      const headers = new HttpHeaders({
        'Authorization': `Bearer ${this.sessionid}`,
        'Content-Type': 'application/json'
      });
      const payload = {
        password: formdata.newPassword,
        confirmPassword: formdata.confirmPassword
      }
      console.log('payload', payload);
      this.http.post(url, payload, { headers }).subscribe({
        next: (response: any) => {
          console.log('Signup successful:', response);
          this.showToast('‚úÖ Password setup successfully', 'success');
          this.currentStep = 8;
        },
        error: (err) => {
          this.loading = false;
          this.showToast('Failed to set Password', 'danger');
        }
      });

    }
  }
}
